<template>
  <div class="container">
    <h2>FRPCÁÆ°ÁêÜÂô®-WebUI</h2>
    <el-row>
      <el-col :span="24">
        <h3>ÂÖ®ÈÉ®ÈÖçÁΩÆÊñá‰ª∂</h3>
        <el-table :data="configs" style="width: 100%">
          <el-table-column prop="name" label="ÈÖçÁΩÆÊñá‰ª∂Âêç"></el-table-column>
          <el-table-column
            label="Êìç‰Ωú"
            width="350">
            <template v-slot:default="scope">
              <el-button type="primary" @click="startProcess(scope.row.name)" :disabled="scope.row.status === 'ËøêË°å‰∏≠üü¢'">ÂêØÂä®</el-button>
              <el-button type="danger" @click="stopProcess(scope.row.name)" :disabled="scope.row.status !== 'ËøêË°å‰∏≠üü¢'">ÂÅúÊ≠¢</el-button>
              <el-button type="warning" @click="showEditConfigDialog(scope.row.name)">ÁºñËæë</el-button>
              <el-button type="info" @click="deleteConfig(scope.row.name)" :disabled="scope.row.status === 'ËøêË°å‰∏≠üü¢'">Âà†Èô§</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="status" label="Áä∂ÊÄÅ"></el-table-column>
        </el-table>

        <el-button class="button-spacing" type="primary" @click="showCreateConfigDialog">Êñ∞Âª∫ÈÖçÁΩÆÊñá‰ª∂</el-button>
<!--        <el-button class="button-spacing" type="primary" @click="showCreateConfigDialog">Êü•Áúã‰ΩøÁî®ËØ¥Êòé</el-button>-->
      </el-col>
    </el-row>
    <el-row v-if="showEditor">
      <el-col :span="24">
        <h3>ÁºñËæëÂô®</h3>
        <el-input type="text" v-model="selectedConfig" placeholder="Êñá‰ª∂Âêç" @change="validateFileName" :disabled="!isNewConfig"></el-input>
        <el-input class="button-spacing" type="textarea" v-model="configContent" :autosize="{ minRows: 10, maxRows: Infinity }" placeholder="ÈÖçÁΩÆÂÜÖÂÆπ"></el-input>
        <el-button class="button-spacing" type="primary" @click="saveConfig">‰øùÂ≠ò</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';

export default {
  data() {
    return {
      configs: [],
      selectedConfig: null,
      configContent: '',
      showEditor: false,
      processes: [],
      isNewConfig: false,
    };
  },
  methods: {
    getConfigs() {
      axios.get('/api/configs')
        .then(response => {
          this.configs = response.data.map(name => {
            const status = this.processes.includes(name) ? 'ËøêË°å‰∏≠üü¢' : 'Â∑≤ÂÅúÊ≠¢üî¥';
            return { name, status };
          });
        });
    },
    getProcesses() {
      axios.get('/api/processes')
        .then(response => {
          this.processes = response.data;
          this.getConfigs();  // Âú®ËøôÈáåË∞ÉÁî® getConfigs() ÂáΩÊï∞
        });
    },
    showCreateConfigDialog() {
      this.showEditor = true;
      this.selectedConfig = '';
      this.configContent = '';
      this.isNewConfig = true;
    },
    showEditConfigDialog(name) {
      this.showEditor = true;
      this.selectedConfig = name;
      this.isNewConfig = false;
      this.getConfig();
    },
    deleteConfig(name) {
      Swal.fire({
        title: 'Á°ÆËÆ§',
        text: '‰Ω†Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÈÖçÁΩÆÊñá‰ª∂ÂêóÔºü',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Âà†Èô§',
        cancelButtonText: 'ÂèñÊ∂à'
      }).then((result) => {
        if (result.isConfirmed) {
          axios.delete(`/api/configs/${name}`)
            .then(response => {
              if (response.data.status === 'success') {
                this.getConfigs();
                Swal.fire('Âà†Èô§ÊàêÂäü', '', 'success');
              }
            });
        }
      });
    },
    getConfig() {
      if (this.selectedConfig) {
        axios.get(`/api/configs/${this.selectedConfig}`)
          .then(response => {
            this.configContent = response.data.content;
          });
      }
    },
    saveConfig() {
      if (this.selectedConfig) {
        const data = { name: this.selectedConfig, content: this.configContent }
        if (this.isNewConfig) {
          axios.post('/api/configs', data)
            .then(response => {
              if (response.data.status === 'success') {
                this.getConfigs();
                Swal.fire('‰øùÂ≠òÊàêÂäü', '', 'success');
              }
            });
        } else {
          const url = `/api/configs/${this.selectedConfig}`;
          axios.put(url, data)
            .then(response => {
              if (response.data.status === 'success') {
                this.getConfigs();
                Swal.fire('‰øùÂ≠òÊàêÂäü', '', 'success');
              }
            });
        }
      }
    },
    startProcess(name) {
      axios.post('/api/processes', { name })
        .then(response => {
          if (response.data.status === 'success') {
            this.getProcesses();
            Swal.fire('ÂêØÂä®ÊàêÂäü', '', 'success');
          }
        });
    },
    stopProcess(name) {
      Swal.fire({
        title: 'Á°ÆËÆ§',
        text: '‰Ω†Á°ÆÂÆöË¶ÅÂÅúÊ≠¢Ëøô‰∏™ËøõÁ®ãÂêóÔºü',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'ÂÅúÊ≠¢',
        cancelButtonText: 'ÂèñÊ∂à'
      }).then((result) => {
        if (result.isConfirmed) {
          axios.delete(`/api/processes/${name}`)
            .then(response => {
              if (response.data.status === 'success') {
                this.getProcesses();
                Swal.fire('ÂÅúÊ≠¢ÊàêÂäü', '', 'success');
              }
            });
        }
      });
    },
    validateFileName() {
      const regex = /^[\w\p{Script=Hani}.-]+(\.toml|\.ini|\.json|\.yaml)?$/u;
      if (!regex.test(this.selectedConfig)) {
        Swal.fire('ÈîôËØØ', 'Êñá‰ª∂ÂêçÂè™ËÉΩÂåÖÂê´‰∏≠Êñá„ÄÅÂ≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏ãÂàíÁ∫øÂíåÁÇπÔºåÂ¶ÇÊûúÊúâÂêéÁºÄÔºåÂè™ËÉΩÊòØtoml„ÄÅini„ÄÅjson„ÄÅyaml', 'error');
        this.selectedConfig = '';
      } else if (this.isNewConfig && this.configs.some(config => config.name.split('.')[0] === this.selectedConfig.split('.')[0])) {
        Swal.fire('ÈîôËØØ', 'Êñá‰ª∂ÂêçÂ∑≤Â≠òÂú®', 'error');
        this.selectedConfig = '';
      }
    }
  },
  created() {
    this.getProcesses();  // Âè™ÈúÄË¶ÅË∞ÉÁî® getProcesses() ÂáΩÊï∞
  }
}
</script>

<style scoped>
.container {
  margin: 20px;
}

.button-spacing {
  margin-top: 15px;
}
</style>
